<template>
  <div class="RightNav">
    <a href="#" id="quit" @click="quit">X</a>
    <ul class="nav">
      <li>
        <a href="#" @click.prevent="showFeat" class="Ft">Features
        <img :src="Featimg" alt="">
        </a>
        <transition
            appear
            name="animate__animated animate__bounce"
            enter-active-class="animate__faster animate__fadeIn"
            leave-active-class="animate__faster animate__fadeOut"
        >
          <div class="Feat_list" v-if="showFeat_list">
            <ul>
              <li><img src="../../public/images/icon-todo.svg" alt=""><span>Todo List</span></li>
              <li><img src="../../public/images/icon-calendar.svg" alt=""><span>Calendar</span></li>
              <li><img src="../../public/images/icon-reminders.svg" alt=""><span>Reminders</span></li>
              <li><img src="../../public/images/icon-planning.svg" alt=""><span>Planning</span></li>
            </ul>
          </div>
        </transition>
      </li>
      <li>
        <a href="#" @click.prevent="showCompany" class="Cp">Company
        <img :src="Companyimg" alt="">
        </a>
        <transition
            appear
            name="animate__animated animate__bounce"
            enter-active-class="animate__faster animate__fadeIn"
            leave-active-class="animate__faster animate__fadeOut"
        >
          <div class="Company_list" v-if="showCompany_list">
            <ul>
              <li><span>History</span></li>
              <li><span>Our Team</span></li>
              <li><span>Blog</span></li>
            </ul>
          </div>
        </transition>
      </li>
      <li><a href="#">Careers</a></li>
      <li><a href="#">About</a></li>
    </ul>
    <div class="RightNav_log">
      <a href="#">Login</a>
      <a href="#" id="Register">Register</a>
    </div>
  </div>
</template>

<script>
export default{
  name:'SideNav',
  data() {
    return {
      showFeat_list: false,
      showCompany_list: false,
      // 顶部导航下拉图案变换
      isFeat: false,
      isCompany: false,
      arrowDown: require('../../public/images/icon-arrow-down.svg'),
      arrowUp: require('../../public/images/icon-arrow-up.svg')
    }
  },
  computed: {
    Featimg() {
      return this.isFeat ? this.arrowUp : this.arrowDown
    },
    Companyimg() {
      return this.isCompany ? this.arrowUp : this.arrowDown
    }
  },
  props:{
    modelValue: Boolean
  },
  emits: ['update:modelValue'],
  methods: {
    quit() {
      this.$emit('update:modelValue', false);
    },
    // Features和Company展开功能
    showFeat() {
      if (this.isFeat === false) {
          this.isFeat = !this.isFeat
          this.showFeat_list = true
      } else {
        // 再次点击关闭展开状态
        this.isFeat = !this.isFeat
        this.showFeat_list = false
      }
    },
    showCompany() {
      if (this.isCompany === false) {
          this.isCompany = !this.isCompany
          this.showCompany_list = true
      } else {
        // 再次点击关闭展开状态
        this.isCompany = !this.isCompany
        this.showCompany_list = false
      }
    }
  }
}
</script>

<style scoped>
.RightNav {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  background-color: rgba(255, 255, 255);
  z-index: 2;
}
#quit {
  position: absolute;
  right: 20px;
  top: 10px;
  border: none;
  background: none;
  font-size: 50px;
  color: rgb(105, 105, 105);
  font-family: "Microsoft PhagsPa";
}
#quit:hover {
  color: #333333;
}
.nav{
  position: relative;
  top: 80px;
}
ul li a{
  font-size: 20px;
  margin: 15px;
  font-family: "Microsoft PhagsPa";
  font-weight: normal;
  z-index: 99;
}
a img{
  width: 13px;
  height: 7px;
  position: relative;
  top: 13px;
  left: 13px;
}
.RightNav_log{
  display: flex;
  flex-direction: column;
  position: relative;
  top: 100px;
  text-align: center;
}
.RightNav_log a{
  margin: 5px 20px;
  padding: 10px 60px;
}
.Feat_list{
  display: block;
}
.Feat_list li{
  margin: 25px 0;
  color: rgb(105, 105, 105);
}
.Feat_list li img{
  position: relative;
  top: 2px;
  margin-right: 15px;
}
.Company_list{
  display: block;
}
.Company_list li{
  margin: 25px 0;
  color: rgb(105, 105, 105);
}
@media (min-width: 1000px) {
  .RightNav{
    display: none;
  }
}

</style>