<template>
  <div class="Header">
    <div class="Header__left">
      <div class="logo">
        <img src="../../public/images/logo.svg" alt="">
      </div>
      <ul>
        <li><a href="#" @click.prevent="showFeat">Features
          <img :src="Featimg" alt="">
        </a></li>
        <li><a href="#" @click.prevent="showCompany">Company
          <img :src="Companyimg" alt="">
        </a></li>
        <li><a href="#">Careers</a></li>
        <li><a href="#">About</a></li>
      </ul>
      <Header_List :showFeat="showFeat_list" :showCompany="showCompany_list"/>
    </div>
    <div class="Header__right">
      <div class="nav_log">
        <a href="#">Login</a>
        <a href="#" id="Register">Register</a>
      </div>
      <a href="#" class="menu-toggle" @click="rightNav">☰</a>
    </div>
  </div>
  <transition
      appear
      name="animate__animated animate__bounce"
      enter-active-class="animate__faster animate__fadeInRight"
      leave-active-class="animate__faster animate__fadeOutRight"
  >
    <SideNav v-model="showNav" v-if="showNav"/>
  </transition>
</template>

<script>
import SideNav from "@/components/SideNav.vue";
import Header_List from "@/components/Header_List.vue";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Headers',
  data() {
    return {
      showNav: false, // 侧边栏默认不显示
      showFeat_list: false,
      showCompany_list: false,
      // 顶部导航下拉图案变换
      isFeat: false,
      isCompany: false,
      arrowDown: require('../../public/images/icon-arrow-down.svg'),
      arrowUp: require('../../public/images/icon-arrow-up.svg')
    }
  },
  components: {
    SideNav,
    Header_List
  },
  computed: {
    Featimg() {
      return this.isFeat ? this.arrowUp : this.arrowDown
    },
    Companyimg() {
      return this.isCompany ? this.arrowUp : this.arrowDown
    }
  },
  methods: {
    // 控制侧边栏是否显示
    rightNav() {
      this.showNav = true
    },
    // Features和Company展开功能
    showFeat() {
      if (this.isFeat === false) {
        if (this.isCompany === true) { // 如果Company处于展开状态
          // 关闭展开状态
          this.isCompany = false
          this.showCompany_list = false
          // 展开Features
          this.isFeat = !this.isFeat
          this.showFeat_list = true
        } else {
          this.isFeat = !this.isFeat
          this.showFeat_list = true
        }
      } else {
        // 再次点击关闭展开状态
        this.isFeat = !this.isFeat
        this.showFeat_list = false
      }

    },
    showCompany() {
      if (this.isCompany === false) {
        if (this.isFeat === true) { // 如果Features处于展开状态
          // 关闭展开状态
          this.isFeat = false
          this.showFeat_list = false
          // 展开Company
          this.isCompany = !this.isCompany
          this.showCompany_list = true
        } else {
          this.isCompany = !this.isCompany
          this.showCompany_list = true
        }
      } else {
        // 再次点击关闭展开状态
        this.isCompany = !this.isCompany
        this.showCompany_list = false
      }
    }
  }
}
</script>

