<template>
  <div class="header_list">
    <transition-group
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__faster animate__fadeIn"
        leave-active-class="animate__faster animate__fadeOut"
    >
    <div class="Feat_list" v-if="showF_list" key="l">
      <ul>
        <li><img src="../../public/images/icon-todo.svg" alt=""><span>Todo List</span></li>
        <li><img src="../../public/images/icon-calendar.svg" alt=""><span>Calendar</span></li>
        <li><img src="../../public/images/icon-reminders.svg" alt=""><span>Reminders</span></li>
        <li><img src="../../public/images/icon-planning.svg" alt=""><span>Planning</span></li>
      </ul>
    </div>
    <div class="Company_list" v-if="showC_list" key="r">
      <ul>
        <li><span>History</span></li>
        <li><span>Our Team</span></li>
        <li><span>Blog</span></li>
      </ul>
    </div>
    </transition-group>
  </div>
</template>

<script>
export default {
  name: "HeaderList",
  data(){
    return {
      showF_list:false,
      showC_list:false,
    }
  },
  props: ['showFeat','showCompany'],
  watch:{
    showFeat(){
      if(this.showFeat === true){
        this.showF_list = true
      }else{
        this.showF_list = false
      }
    },
    showCompany(){
      if(this.showCompany === true){
        this.showC_list = true
      }else{
        this.showC_list = false
      }
    }
  }
}
</script>

<style scoped>
.header_list {
  text-align: center;
  display: flex;
  flex-direction: row;
  position: absolute;
  top: 60px;
  left: 185px;
}
.Feat_list {
  width: 140px;
  height: 140px;
  margin-right: 45px;
  background-color: rgb(255, 255, 255);
  border-radius: 12px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 10px 0 rgba(0, 0, 0, 0.1);
}

.Company_list {
  position: absolute;
  left: 180px;
  width: 110px;
  height: 100px;
  background-color: rgb(255, 255, 255);
  border-radius: 12px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 6px 10px 0 rgba(0, 0, 0, 0.1);
}
ul {
  display: flex;
  flex-direction: column;
  padding: 0 6px;
  text-align: left;
  color: rgb(105, 105, 105);
  font-size: 13px;
}
li {
  margin: 0 0 10px 0;
}
li img{
  position: relative;
  top: 4px;
  margin-right: 10px;
}
@media (max-width: 1000px) {
  .header_list{
    display: none;
  }
}
</style>